import React, { useContext } from "react";
import { Card, Typography } from "antd";
import { CovidContext } from "../../contextAPI/covidDataContext/CovidContextProvider";
import "../css/dataCard.css";

const { Title } = Typography;
const gridStyle = {
  width: "50%",
  textAlign: "center",
  height: "150px",
};

function CardForData({
  filterProvince,
  filterAge,
  filterAgeArray,
  filterGender,
}) {
  // console.log("filterProvince", filterProvince);
  const { data, isLoading } = useContext(CovidContext);
  // console.log("data", data);

  return (
    // {!isLoading? } return (<div
    <div className="data-card" style={{}}>
      <Card
        title={
          <Title level={4}>
            Data: Total Cases <br /> Location:{" "}
            {filterProvince === "" || filterProvince === "All Nepal"
              ? `Whole Nepal`
              : filterProvince}{" "}
            <br />
            {filterAge !== "" && filterAge !== "All Age Group" ? (
              <div>
                Age Group: {filterAgeArray[3]} <br />
              </div>
            ) : (
              ""
            )}
            {filterGender !== "" && filterGender !== "All Gender"
              ? `Gender:  ${
                  filterGender === "infectedMaleCount"
                    ? "Male"
                    : filterGender === "infectedFemaleCount"
                    ? "Female"
                    : ""
                }`
              : ""}
          </Title>
        }
        bordered={false}
        style={{ width: "100%", textAlign: "center" }}
        bodyStyle={{ maxHeight: "60vh", overflow: "auto" }}
      >
        {filterProvince === "" || filterProvince === "All Nepal"
          ? data.districtNames.map((district) => (
              <Card.Grid style={gridStyle}>
                <span style={{ fontSize: 13 }}>
                  <b>{district}</b>
                  {filterAge !== "" && filterAge !== "All Age Group" ? (
                    <span>
                      <br /> <b>{filterAgeArray[3]}</b>
                    </span>
                  ) : null}
                </span>
                <br />
                Total:{" "}
                {filterAge === "" || filterAge === "All Age Group"
                  ? Object.values(
                      data.districtData.find((total) => {
                        return String(Object.keys(total)) === String(district);
                      })
                    )[0]
                  : filterAge && filterAge !== "All Age Group"
                  ? data.geoJson.features.find(
                      (feature) => feature.properties.Name === district
                    ).properties[filterAgeArray[0]]
                  : ""}{" "}
                <br />
                {filterGender === "infectedMaleCount" ||
                filterGender === "All Gender" ||
                filterGender === "" ? (
                  <div>
                    Male:{" "}
                    {filterAge === "" || filterAge === "All Age Group"
                      ? Object.values(
                          data.maleData.find(
                            (item) =>
                              String(Object.keys(item)) === String(district)
                          )
                        )[0]
                      : data.geoJson.features.find(
                          (feature) => feature.properties.Name === district
                        ).properties[filterAgeArray[1]]}
                  </div>
                ) : (
                  ""
                )}
                {filterGender === "infectedFemaleCount" ||
                filterGender === "All Gender" ||
                filterGender === "" ? (
                  <div>
                    Female:{" "}
                    {filterAge === "" || filterAge === "All Age Group"
                      ? Object.values(
                          data.femaleData.find(
                            (item) =>
                              String(Object.keys(item)) === String(district)
                          )
                        )[0]
                      : data.geoJson.features.find(
                          (feature) => feature.properties.Name === district
                        ).properties[filterAgeArray[2]]}
                  </div>
                ) : (
                  ""
                )}
              </Card.Grid>
            ))
          : data.geoJson.features
              .filter(
                (feature) => feature.properties.Province === filterProvince
              )
              .map((feature) => (
                <Card.Grid style={gridStyle}>
                  <span style={{ fontSize: 13 }}>
                    <b>{feature.properties.Name}</b>
                    {filterAge !== "" && filterAge !== "All Age Group" ? (
                      <span>
                        <br /> <b>{filterAgeArray[3]}</b>
                      </span>
                    ) : null}
                  </span>
                  <br />
                  Total:{" "}
                  {filterAge === "" || filterAge === "All Age Group"
                    ? feature.properties.totalInfected
                    : feature.properties[filterAgeArray[0]]}{" "}
                  <br />
                  {filterGender === "infectedMaleCount" ||
                  filterGender === "All Gender" ||
                  filterGender === "" ? (
                    <div>
                      {" "}
                      Male:{" "}
                      {filterAge === "" || filterAge === "All Age Group"
                        ? feature.properties.infectedMaleCount
                        : feature.properties[filterAgeArray[1]]}
                    </div>
                  ) : (
                    ""
                  )}
                  {filterGender === "infectedFemaleCount" ||
                  filterGender === "All Gender" ||
                  filterGender === "" ? (
                    <div>
                      Female:{" "}
                      {filterAge === "" || filterAge === "All Age Group"
                        ? feature.properties.infectedFemaleCount
                        : feature.properties[filterAgeArray[2]]}
                    </div>
                  ) : (
                    ""
                  )}
                </Card.Grid>
              ))}
      </Card>
    </div>
  );
}

export default CardForData;
