import React, { useState, useEffect, useRef, useContext } from "react";
import districts from "../../geoData/nepal.json";
import L from "leaflet";
import _ from "lodash";
import { config } from "../../config";
import "../css/map.css";
import { CovidContext } from "../../contextAPI/covidDataContext/CovidContextProvider";
import CustomUseEffect from "./CustomUseEffect";

const BASE_URL = `${config.BASE_URL}`;

// console.log("Data from json:", districts);

function DataMergeMap({
  filterProvince,
  filterAge,
  filterAgeArray,
  filterGender,
}) {
  const { data } = useContext(CovidContext);
  // console.log("maleData: ", maleData);
  const [markerPosition, setMarkerPosition] = useState({
    lat: 28.5947038,
    lng: 84.2062449,
  });

  const mapRef = useRef(null);
  //   const mapStyle = { marginRight: 40 };
  useEffect(() => {
    mapRef.current = L.map("map", {
      center: markerPosition,
      zoom: 7,
      zoomControl: true,
      doubleClickZoom: false,
      closePopupOnClick: true,
      // dragging: false,
      // zoomSnap: false,
      // zoomDelta: false,
      // trackResize: false,
      // touchZoom: false,
      scrollWheelZoom: false,
      attributionControl: false,
    });
  }, [markerPosition]);

  const geoJsonRef = useRef(null);
  const nepalRef = useRef(null);
  const legendControl = useRef(null);
  const infoControl = useRef(null);
  CustomUseEffect(() => {
    // console.log("Total district count: ", totalDistrictData);

    // console.log(data.geoJson);

    const getColor = (d) => {
      return d == 0
        ? "#2ca25f"
        : d <= 200
        ? "#fd8d3c"
        : d <= 400
        ? "#f03b20"
        : "#bd0026";
    };

    const style = (feature) => {
      // const feature = args[0][0];
      return {
        fillColor:
          (filterAge === "" || filterAge === "All Age Group") &&
          (filterGender === "" || filterGender === "All Gender")
            ? getColor(feature.properties.totalInfected)
            : filterAge &&
              filterAge !== "All Age Group" &&
              (filterGender === "" || filterGender === "All Gender")
            ? getColor(feature.properties[filterAge])
            : filterAge &&
              filterAge !== "All Age Group" &&
              filterGender &&
              filterGender === "infectedMaleCount"
            ? getColor(feature.properties[filterAgeArray[1]])
            : (filterAge === "" || filterAge === "All Age Group") &&
              filterGender &&
              filterGender !== "All Gender"
            ? getColor(feature.properties[filterGender])
            : getColor(feature.properties[filterAgeArray[2]]),
        // fillColor: "green",
        weight: 1.4,
        opacity: 0.7,
        color: "white",
        dashArray: "3",
        fillOpacity: 0.7,
        marginTop: 20,
      };
    };

    function highlightFeature(e) {
      let layer = e.target;

      layer.setStyle({
        weight: 2,
        color: "black",
        dashArray: "",
        fillOpacity: 0.7,
      });

      if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
        layer.bringToFront();
      }
      infoControl.current.update(layer.feature.properties);
    }

    // function zoomToFeature(e) {
    //   mapRef.current.fitBounds(e.target.getBounds());
    // }

    function resetHighlight(e) {
      geoJsonRef.current.resetStyle(e.target);
      infoControl.current.update();
    }

    function onEachFeature(feature, layer) {
      layer.on({
        mouseover: highlightFeature,
        mouseout: resetHighlight,
        // click: zoomToFeature,
      });
    }

    // Custom Info Control
    if (infoControl.current === null) infoControl.current = L.control();

    // console.log(document.getElementsByClassName("leaflet-container"));
    infoControl.current.onAdd = function (map) {
      // L.DomUtil.removeClass(L.DomUtil.get("map"), "info");

      // console.log(info._map);
      // if (!L.DomUtil.hasClass(document.body, "info"))
      this._div = L.DomUtil.create("div", "info"); // create a div with a class "info"
      this.update();
      return this._div;
    };

    // method that we will use to update the control based on feature properties passed
    infoControl.current.update = function (props) {
      this._div.innerHTML =
        "<h3><b>District Data</b> </h3>" +
        (props
          ? `<h3><b>${props.Name}</b></h3><b>  ${
              filterAge !== "" && filterAge !== "All Age Group"
                ? "<br/>Total (" +
                  filterAgeArray[3] +
                  ") : " +
                  props[filterAgeArray[0]]
                : "Total Infected: " + props.totalInfected
            } ${
              filterGender === "infectedMaleCount" ||
              filterGender === "All Gender" ||
              filterGender === ""
                ? "</b><br /> <b> Male" +
                  (filterAge !== "" && filterAge !== "All Age Group"
                    ? " (" +
                      filterAgeArray[3] +
                      ") : " +
                      props[filterAgeArray[1]]
                    : " :" + props.infectedMaleCount)
                : ""
            } ${
              filterGender === "infectedFemaleCount" ||
              filterGender === "All Gender" ||
              filterGender === ""
                ? "</b><br /> <b> Female" +
                  (filterAge !== "" && filterAge !== "All Age Group"
                    ? " (" +
                      filterAgeArray[3] +
                      ") : " +
                      props[filterAgeArray[2]]
                    : " :" + props.infectedFemaleCount)
                : ""
            } </b> `
          : "<b>Hover over a District</b>");
    };

    infoControl.current.addTo(mapRef.current);

    // Legend Control
    if (legendControl.current === null)
      legendControl.current = L.control({ position: "bottomleft" });
    // !legendControl.current._map
    //   ? (legendControl.current = L.control({ position: "bottomright" }))
    //   : null;

    legendControl.current.onAdd = function (map) {
      let div = L.DomUtil.create("div", "info legend"),
        grades = [0, 1, 200, 400];
      // labels = [];

      div.innerHTML += "<b>Number of cases</b> <br />";

      // loop through our density intervals and generate a label with a colored square for each interval
      for (let i = 0; i < grades.length; i++) {
        div.innerHTML +=
          grades[i] == 0
            ? '<i style="background:#2ca25f"></i> 0 <br/>'
            : '<i style="background:' +
              getColor(grades[i] + 1) +
              '"></i> ' +
              grades[i] +
              (grades[i + 1] ? "&ndash;" + grades[i + 1] + "<br>" : "+");
      }

      return div;
    };

    legendControl.current.addTo(mapRef.current);

    const addLayerToMap = () => {
      //Add layer for inactive portions after applying Province filter
      if (nepalRef.currrent != null) {
        nepalRef.current.clearLayers();
      }

      nepalRef.current = null;
      nepalRef.current = L.geoJSON(data.geoJson, {
        style: {
          // fillColor: getColor(feature.properties.totalInfected),
          fillColor: "#deebf7",
          weight: 1.4,
          opacity: 2,
          color: "white",
          dashArray: "3",
          fillOpacity: 0.2,
          marginTop: 20,
        },
      }).addTo(mapRef.current);

      //Add layer for districts geojson with heatmap.When province filter is applied, higlight only the selected province, over another layer of districts geojson which is inactive (nepalRef)
      if (mapRef.current.hasLayer(geoJsonRef.current)) {
        mapRef.current.removeLayer(geoJsonRef.current);
      }
      if (geoJsonRef.currrent != null) {
        geoJsonRef.current.clearLayers();
      }

      geoJsonRef.current = null;
      geoJsonRef.current = L.geoJSON(data.geoJson, {
        filter: function (feature, layer) {
          if (!filterProvince || filterProvince === "All Nepal") return true;
          else return feature.properties.Province === filterProvince;
        },
        style: style,
        onEachFeature: onEachFeature,
      }).addTo(mapRef.current);
    };

    addLayerToMap();
  }, [data.geoJson, filterProvince, filterAge, filterGender]);
  return (
    <>
      {/* <FilterButton onClick={onClick} /> */}
      <div
        id="map"
        ref={mapRef}
        style={{
          width: "100%",
          height: "75vh",
          backgroundColor: "white",
          // marginTop: 40,
        }}
      ></div>
    </>
  );
}

export default DataMergeMap;
