import React from "react";
import { Menu, Dropdown } from "antd";
import { DownOutlined } from "@ant-design/icons";

function AgeFilterButton({ onClick, filterAge }) {
  const menu = (
    <Menu onClick={(key) => onClick(key)}>
      <Menu.Item key="All Age Group">All Age Group</Menu.Item>
      <Menu.Item key="zeroToTenTotalStat">0 - 10 years</Menu.Item>
      <Menu.Item key="elevenToTwentyTotalStat">11 - 20 years</Menu.Item>
      <Menu.Item key="twentyOneToThirtyTotalStat">21 - 30 years</Menu.Item>
      <Menu.Item key="thirtyOneToFortyTotalStat">31 - 40 years</Menu.Item>
      <Menu.Item key="fortyOneToFiftyTotalStat">41 - 50 years</Menu.Item>
      <Menu.Item key="fiftyOneToSixtyTotalStat">51 - 60 years</Menu.Item>
      <Menu.Item key="sixtyOneToSeventyTotalStat">61 - 70 years</Menu.Item>
      <Menu.Item key="seventyOneToEightyTotalStat">71 - 80 years</Menu.Item>
      <Menu.Item key="eightyPlusTotalStat">80+ years</Menu.Item>
    </Menu>
  );

  return (
    <Dropdown.Button
      overlay={menu}
      placement="bottomCenter"
      arrow="true"
      icon={<DownOutlined />}
      style={{ marginLeft: "20px" }}
    >
      {filterAge === "" || filterAge === "All Age Group"
        ? "Select Age Group"
        : filterAge === "zeroToTenTotalStat"
        ? "0 - 10 years"
        : filterAge === "elevenToTwentyTotalStat"
        ? "11 - 20 years"
        : filterAge === "twentyOneToThirtyTotalStat"
        ? "21 - 30 years"
        : filterAge === "thirtyOneToFortyTotalStat"
        ? "31 - 40 years"
        : filterAge === "fortyOneToFiftyTotalStat"
        ? "41 - 50 years"
        : filterAge === "fiftyOneToSixtyTotalStat"
        ? "51 - 60 years"
        : filterAge === "sixtyOneToSeventyTotalStat"
        ? "61 - 70 years"
        : filterAge === "seventyOneToEightyTotalStat"
        ? "71 - 80 years"
        : filterAge === "eightyPlusTotalStat"
        ? "80+ years"
        : null}
    </Dropdown.Button>
  );
}

export default AgeFilterButton;
