import React from "react";
import { Menu, Dropdown } from "antd";
import { DownOutlined } from "@ant-design/icons";

function GenderFilterButton({ onClick, filterGender }) {
  const menu = (
    <Menu onClick={(key) => onClick(key)}>
      <Menu.Item key="All Gender">All Gender</Menu.Item>
      <Menu.Item key="infectedMaleCount">Male</Menu.Item>
      <Menu.Item key="infectedFemaleCount">Female</Menu.Item>
    </Menu>
  );

  return (
    <>
      <Dropdown.Button
        overlay={menu}
        placement="bottomCenter"
        arrow="true"
        icon={<DownOutlined />}
        style={{ marginLeft: "20px" }}
      >
        {filterGender === "" || filterGender === "All Gender"
          ? "Select Gender"
          : filterGender === "infectedMaleCount"
          ? "Male"
          : "Female"}
      </Dropdown.Button>
    </>
  );
}

export default GenderFilterButton;
