import React from "react";
import { Menu, Dropdown, message, Button } from "antd";
import { DownOutlined } from "@ant-design/icons";

function ProvinceFilterButton({ onClick, filterProvince }) {
  const menu = (
    <Menu onClick={(key) => onClick(key)}>
      <Menu.Item key="All Nepal">All Nepal</Menu.Item>
      <Menu.Item key="Province 1">Province 1</Menu.Item>
      <Menu.Item key="Province 2">Province 2</Menu.Item>
      <Menu.Item key="Bagmati">Bagmati</Menu.Item>
      <Menu.Item key="Gandaki">Gandaki</Menu.Item>
      <Menu.Item key="Province 5">Province 5</Menu.Item>
      <Menu.Item key="Karnali">Karnali</Menu.Item>
      <Menu.Item key="Sudurpaschim">Sudurpaschim</Menu.Item>
    </Menu>
  );

  return (
    <>
      <Dropdown.Button
        overlay={menu}
        placement="bottomCenter"
        arrow="true"
        icon={<DownOutlined />}
      >
        {filterProvince === "" || filterProvince === "All Nepal"
          ? "Select Province"
          : filterProvince}
      </Dropdown.Button>
    </>
  );
}

export default ProvinceFilterButton;
