import React, { useState, useContext } from "react";
import _ from "lodash";
import "../css/map.css";
// import FilterButton from "../components/FilterButton";
import { Row, Col, Typography } from "antd";
import DataMergeMap from "../../mapRepresentation/components/DataMergeMap";
import CardForData from "../components/CardForData";
import ProvinceFilterButton from "../components/ProvinceFilterButton";
import AgeFilterButton from "../components/AgeFilterButton";
import GenderFilterButton from "../components/GenderFilterButton";
import { CovidContext } from "../../contextAPI/covidDataContext/CovidContextProvider";
import Skeleton from "react-loading-skeleton";

const { Title } = Typography;

// const BASE_URL = `${config.BASE_URL}`;

function MapDataContainer() {
  const { isLoading } = useContext(CovidContext);

  const [filterProvince, setFilterProvince] = useState("");
  const [filterAge, setFilterAge] = useState("");
  const [filterAgeArray, setFilterAgeArray] = useState([]);
  const [filterGender, setFilterGender] = useState("");

  // const filterProvince

  //functioin for filter
  const filterProvinceClick = ({ key }) => {
    // message.info(`Click on item ${key}`);
    setFilterProvince(key);
  };

  const filterGenderClick = ({ key }) => {
    // message.info(`Click on item ${key}`);
    setFilterGender(key);
  };

  const filterAgeClick = ({ key }) => {
    // message.info(`Click on item ${key}`);
    setFilterAge(key);
    let filterAgeStat = {};
    if (key === "zeroToTenTotalStat")
      filterAgeStat = [
        "zeroToTenTotalStat",
        "zeroToTenMaleStat",
        "zeroToTenFemaleStat",
        "0 - 10 years",
      ];
    if (key === "elevenToTwentyTotalStat")
      filterAgeStat = [
        "elevenToTwentyTotalStat",
        "elevenToTwentyMaleStat",
        "elevenToTwentyFemaleStat",
        "11 - 20 years",
      ];
    if (key === "twentyOneToThirtyTotalStat")
      filterAgeStat = [
        "twentyOneToThirtyTotalStat",
        "twentyOneToThirtyMaleStat",
        "twentyOneToThirtyFemaleStat",
        "21 - 30 years",
      ];
    if (key === "thirtyOneToFortyTotalStat")
      filterAgeStat = [
        "thirtyOneToFortyTotalStat",
        "thirtyOneToFortyMaleStat",
        "thirtyOneToFortyFemaleStat",
        "31 - 40 years",
      ];
    if (key === "fortyOneToFiftyTotalStat")
      filterAgeStat = [
        "fortyOneToFiftyTotalStat",
        "fortyOneToFiftyMaleStat",
        "fortyOneToFiftyFemaleStat",
        "41 -50 years",
      ];
    if (key === "fiftyOneToSixtyTotalStat")
      filterAgeStat = [
        "fiftyOneToSixtyTotalStat",
        "fiftyOneToSixtyMaleStat",
        "fiftyOneToSixtyFemaleStat",
        "51 - 60 years",
      ];
    if (key === "sixtyOneToSeventyTotalStat")
      filterAgeStat = [
        "sixtyOneToSeventyTotalStat",
        "sixtyOneToSeventyMaleStat",
        "sixtyOneToSeventyFemaleStat",
        "61 - 70 years",
      ];
    if (key === "seventyOneToEightyTotalStat")
      filterAgeStat = [
        "seventyOneToEightyTotalStat",
        "seventyOneToEightyMaleStat",
        "seventyOneToEightyFemaleStat",
        "71 - 80 years",
      ];
    if (key === "eightyPlusTotalStat")
      filterAgeStat = [
        "eightyPlusTotalStat",
        "eightyPlusMaleStat",
        "eightyPlusFemaleStat",
        "80+ years",
      ];

    setFilterAgeArray(filterAgeStat);
  };

  return (
    <>
      <Row style={{ marginTop: 30 }}>
        <Col span={22} offset={1}>
          <Title level={4}>
            Map Representation of Covid-19 Nepal Data |{" "}
            <span style={{ fontSize: 16 }}>2020</span>
          </Title>
        </Col>
      </Row>
      <Row
        justify="center"
        // style={{
        //   marginTop: 10,
        //   marginRight: 40,
        //   marginBottom: 10,
        //   marginLeft: 15,
        // }}
      >
        {/* <Row justify="center" style={{}}> */}
        <Col span={15} style={{}}>
          <ProvinceFilterButton
            onClick={filterProvinceClick}
            filterProvince={filterProvince}
          />
          <AgeFilterButton onClick={filterAgeClick} filterAge={filterAge} />
          <GenderFilterButton
            onClick={filterGenderClick}
            filterGender={filterGender}
          />
          {/* <Card style={{}}> */}
          {/* <LeafletMap /> */}
          {/* <MapComponent /> */}
          <DataMergeMap
            filterProvince={filterProvince}
            filterAge={filterAge}
            filterAgeArray={filterAgeArray}
            filterGender={filterGender}
          />

          {/* </Card> */}
        </Col>
        <Col span={7}>
          {isLoading ? (
            <Skeleton count={1} height={500} />
          ) : (
            <CardForData
              filterProvince={filterProvince}
              filterAge={filterAge}
              filterAgeArray={filterAgeArray}
              filterGender={filterGender}
            />
          )}
        </Col>
        {/* </Row> */}
      </Row>
    </>
  );
}

export default MapDataContainer;
